import Axios from 'axios';
import querystring from "querystring";
Axios.defaults.baseURL = "http://localhost:8090";
Axios.defaults.headers = {'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8'}
export default (
    {
        url,
        method,
        params,  //get参数
        data,  //post参数

    }
) => {
    let config;
    if(params==null&&data!=null){
        config = {
            url,
            method,
           data:querystring.stringify(data)   //数据格式为'application/x-www-form-urlencoded
        };
    }else if(params!=null&&data==null){
        config = {
            url,
            method,
            params
        };
    }else{
        config = {
            url,
            method
        };
    }
  
    console.log(config);
    return Axios(config);

}